/* AppenBasis-400normal */
@font-face {
  font-family: 'Appen Basis';
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src:
    local('AppenBasis Regular '),
    local('AppenBasis-Regular'),
    url('./files/AppenBasis-Regular.otf') format('opentype'); /* Modern Browsers */
}

/* AppenBasis-400italic */
@font-face {
  font-family: 'Appen Basis';
  font-style: italic;
  font-display: swap;
  font-weight: 400;
  src:
    local('AppenBasis Italic '),
    local('AppenBasis-Italic'),
    url('./files/AppenBasis-Italic.otf') format('opentype'); /* Modern Browsers */
}
