/* AppenBasis-900normal */
@font-face {
  font-family: 'Appen Basis';
  font-style: normal;
  font-display: swap;
  font-weight: 900;
  src:
    local('AppenBasis Bold '),
    local('AppenBasis-Bold'),
    url('./files/AppenBasis-Bold.otf') format('opentype'); /* Modern Browsers */
}

/* AppenBasis-900italic */
@font-face {
  font-family: 'Appen Basis';
  font-style: italic;
  font-display: swap;
  font-weight: 900;
  src:
    local('AppenBasis BoldItalic '),
    local('AppenBasis-BoldItalic'),
    url('./files/AppenBasis-BoldItalic.otf') format('opentype'); /* Modern Browsers */
}