/* AppenBasis-700normal */
@font-face {
  font-family: 'Appen Basis';
  font-style: normal;
  font-display: swap;
  font-weight: 700;
  src:
    local('AppenBasis Medium '),
    local('AppenBasis-Medium'),
    url('./files/AppenBasis-Medium.otf') format('opentype'); /* Modern Browsers */
}

/* AppenBasis-700italic */
@font-face {
  font-family: 'Appen Basis';
  font-style: italic;
  font-display: swap;
  font-weight: 700;
  src:
    local('AppenBasis MediumItalic '),
    local('AppenBasis-MediumItalic'),
    url('./files/AppenBasis-MediumItalic.otf') format('opentype'); /* Modern Browsers */
}
